"use client";
import React from 'react';
import { Users, BookOpen, ShieldCheck, Zap, Globe, Lock } from 'lucide-react';
import { motion, useMotionValue, useTransform } from 'framer-motion';
import styles from './FeaturesSection.module.css';

const TiltCard = ({ children, className, delay }) => {
    const x = useMotionValue(0);
    const y = useMotionValue(0);
    const rotateX = useTransform(y, [-100, 100], [5, -5]); // Inverted for tilt
    const rotateY = useTransform(x, [-100, 100], [-5, 5]);

    function onMouseMove(event) {
        const rect = event.currentTarget.getBoundingClientRect();
        const centerX = (rect.left + rect.right) / 2;
        const centerY = (rect.top + rect.bottom) / 2;
        x.set(event.clientX - centerX);
        y.set(event.clientY - centerY);
    }

    function onMouseLeave() {
        x.set(0);
        y.set(0);
    }

    return (
        <motion.div
            className={className}
            style={{ rotateX, rotateY, perspective: 1000 }}
            onMouseMove={onMouseMove}
            onMouseLeave={onMouseLeave}
            initial={{ opacity: 0, y: 50 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.8, delay, ease: "backOut" }}
        >
            {children}
        </motion.div>
    );
};

const BentoCard = ({ icon: Icon, title, description, variant = '', delay = 0 }) => (
    <TiltCard
        className={`${styles.card} ${styles[variant] || ''}`}
        delay={delay}
    >
        <div className={styles.cardContent}>
            <div className={styles.cardIcon}>
                <Icon size={32} />
            </div>
            <h3 className={styles.cardTitle}>{title}</h3>
            <p className={styles.cardDesc}>{description}</p>
        </div>
        <div className={styles.visual}>
            {/* Texture overlay for 'paper' feel */}
            <div style={{
                position: 'absolute', inset: 0,
                backgroundImage: 'url("data:image/svg+xml,%3Csvg viewBox=\'0 0 200 200\' xmlns=\'http://www.w3.org/2000/svg\'%3E%3Cfilter id=\'noiseFilter\'%3E%3CfeTurbulence type=\'fractalNoise\' baseFrequency=\'0.65\' numOctaves=\'3\' stitchTiles=\'stitch\'/%3E%3C/filter%3E%3Crect width=\'100%25\' height=\'100%25\' filter=\'url(%23noiseFilter)\' opacity=\'0.1\'/%3E%3C/svg%3E")',
                opacity: 0.4,
                mixBlendMode: 'overlay'
            }} />
        </div>
    </TiltCard>
);

