"use client";
import React, { useRef } from 'react';
import Link from 'next/link';
import { ArrowRight } from 'lucide-react';
import { motion, useMotionTemplate, useMotionValue, useSpring } from 'framer-motion';
import styles from './HeroSection.module.css';

const HeroSection = () => {
    const sectionRef = useRef(null);
    const mouseX = useMotionValue(0);
    const mouseY = useMotionValue(0);

    // Smooth spring for the spotlight
    const springX = useSpring(mouseX, { stiffness: 100, damping: 20 });
    const springY = useSpring(mouseY, { stiffness: 100, damping: 20 });

    // Parallax background (inverted movement)
    const backgroundX = useSpring(mouseX, { stiffness: 50, damping: 30 });
    const backgroundY = useSpring(mouseY, { stiffness: 50, damping: 30 });

    const spotlightMask = useMotionTemplate`radial-gradient(250px circle at ${springX}px ${springY}px, black 0%, transparent 100%)`;

    function onMouseMove({ currentTarget, clientX, clientY }) {
        const { left, top } = currentTarget.getBoundingClientRect();
        mouseX.set(clientX - left);
        mouseY.set(clientY - top);
    }

    return (
        <section
            ref={sectionRef}
            className={styles.section}
            onMouseMove={onMouseMove}
        >
            <motion.div
                className={styles.backgroundBlob}
                style={{ x: backgroundX, y: backgroundY, translateX: '-50%', translateY: '-50%' }}
            />

            <div className="container">
                <div style={{ maxWidth: '1000px', margin: '0 auto', position: 'relative', zIndex: 10 }}>
                    <div style={{ position: 'relative', display: 'inline-block' }}>
                        <h1 className={styles.title}>
                            University <br />
                            <i>Management Reimagined</i>
                        </h1>

                        {/* Spotlight Layer */}
                        <motion.h1
                            className={styles.title}
                            style={{
                                position: 'absolute',
                                top: 0,
                                left: 0,
                                color: '#0071E3',
                                WebkitMaskImage: spotlightMask,
                                maskImage: spotlightMask,
                                zIndex: 2,
                                pointerEvents: 'none'
                            }}
                        >
                            University <br />
                            <i>Management Reimagined</i>
                        </motion.h1>
                    </div>

                    <motion.p
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.2 }}
                        className={styles.description}
                    >
                        Designed for the future of education. Editorial refinement meets robust engineering.
                    </motion.p>

                    <div className={styles.actions}>
                        <Link href="/login" className={styles.primaryBtn}>
                            Experience Now
                        </Link>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default HeroSection;
